import React from "react";
import styles from "./home.module.css";
import List from "./view/contentList/list";
import Photo from "./view/photoWrapper/phototWrapper";
import Observable from "./model/Countdown";

class home extends React.Component {
  constructor() {
    super(...arguments);
    this.state = {
      Hour: new Date(this.props.remainTime).getHours(),
      Minute: new Date(this.props.remainTime).getMinutes(),
      Second: new Date(this.props.remainTime).getSeconds()
    };
    const getObservable = Observable();
    this.reducerObservable$ = getObservable.reducerObservable$;
    this.ObservableTurnover = getObservable.ObservableTurnover.subscribe();
    this.reducerObservable$.next({
      type: "presentTime",
      timeList: this.state
    });
  }
  componentWillUnmount() {
    this.ObservableTurnover.unsubscribe();
    this.reducerObservable$.unsubscribe();
  }
  componentDidMount() {
    this.reducerObservable$.subscribe(timeList => this.setState(timeList));
  }
  render() {
    const {
      primaryPicUrl,
      originPrice,
      activityPrice,
      nextStartTime,
      title
    } = this.props;
    const { Hour, Minute, Second } = this.state;
    return (
      <div className={styles.frame}>
        <List
          title={title}
          hour={Hour}
          minute={Minute}
          second={Second}
          nextTime={new Date(nextStartTime).getHours()}
        />
        <Photo
          picUrl={primaryPicUrl}
          originalPrice={originPrice}
          discountsPrice={activityPrice}
        />
      </div>
    );
  }
}
export default home;



