import React from "react";
import WrappedComponent from "animationLoad/wrapper";
import CarouselPhoto from "./carouselPhoto/carouselPhoto";
import ServicePolicy from "./servicePolicy/servicePolicy";
import ProductMake from "./productMake/productMake";
import LimitedTime from "./limitedTime/home";
import ConnectSwipe from "./swipeListCopy/index";
import Discount from "./discountGet/discount";
import ItemList from "./itemList/itemList";
import Footer from "./footer/footer";
import styles from "./home.module.css";
import ReturnTop from "returnTop/index";

const newProducts = {
  title: "新品首发",
  headerBackground: " #EEF5FC",
  headerColor: " #8BA0B6",
  allBackground: " #D8E5F1"
};
const popularityProducts = {
  title: "人气推荐 · 好物精选 ",
  headerBackground: " #FEF7E3",
  headerColor: " #B4A078",
  allBackground: " rgb(244, 233, 203)"
};
const limitedState = {
  title: "严选限时购"
};

const Index = ({
  policyDescList,
  focusList,
  tagList,
  newItemList,
  popularItemList,
  saleIndexVO,
  headCateList,
  flashSaleIndexVO,
  cateList
}) => (
  <div className={styles.wrapper}>
    <CarouselPhoto focusList={focusList} />
    <ServicePolicy policyName={policyDescList} />
    <ProductMake
      overallStyle={styles.overallList}
      header={"品牌制造商直供 "}
      iconName={"directionRight"}
      productList={tagList.slice(0, 4)}
    />
    <ConnectSwipe
      {...newProducts}
      swipeId={"ConnectSwipe1"}
      itemList={newItemList}
      overallStyle={styles.overallList}
    />
    <ConnectSwipe
      {...popularityProducts}
      swipeId={"ConnectSwipe2"}
      itemList={popularItemList}
      overallStyle={styles.overallList}
    />
    <LimitedTime {...limitedState} {...flashSaleIndexVO} />
    <Discount picUrl={saleIndexVO.picUrl} />
    {cateList.map(({ name, itemList, id, wait = true }) => (
      <React.Fragment key={id}>
        <ItemList name={name} itemList={itemList.slice(0, 7)} wait={wait} />
      </React.Fragment>
    ))}
    <Footer />
    <ReturnTop />
  </div>
);

export default WrappedComponent(Index);



