import React from "react";
import styles from "./list.module.css";

const list = ({ title, hour, minute, second, nextTime }) => (
  <div className={styles}>
    <div className={styles.header}>{title}</div>
    <div className={styles.countdownList}>
      <div className={styles.countdownItem}>{hour}</div>
      <div className={styles.separate}>:</div>
      <div className={styles.countdownItem}>{minute}</div>
      <div className={styles.separate}>:</div>
      <div className={styles.countdownItem}>{second}</div>
    </div>
    <div className={styles.nextTime}>
      下一场
      {nextTime}
      :00开始
    </div>
  </div>
);

export default list;



