import React from "react";
import styles from "./productList.module.css";
import { Link } from "@reach/router";

const productList = ({ ListState ,id}) => {
  return (
    <div className={`${styles.moveWrapper} swipe-wrap`} id={id}>
      <ol className={`${styles.list} swiper-wrapper`}>
        {ListState.map(
          ({
            colorNum,
            iconText,
            listPicUrl,
            name,
            retailPrice,
            simpleDesc,
            id
          }) => (
            <React.Fragment key={id}>
              <li className={`${styles.item} swiper-slide`}>
               <Link to={`./detail/item/${id}`}>
                  <div className={styles.photoWrapper}>
                    {colorNum ? (
                      <div className={styles.specification}>
                        {colorNum}
                        <div>色</div>
                        <div>可</div>
                        <div>选</div>
                      </div>
                    ) : (
                      ""
                    )}
                    {iconText ? (
                      <div className={styles.specification}>
                        <div className={styles.toVertical}>{iconText}</div>
                      </div>
                    ) : (
                      ""
                    )}
                    <img
                      data-src={listPicUrl}
                      className={`${styles.photo} swiper-lazy`}
                      alt={""}
                    />
                  </div>
                  <div className={styles.innerText}>
                    <div className={styles.name}>{name}</div>
                    <div className={styles.publicity}>{simpleDesc}</div>
                    <span className={styles.money}>{`¥${retailPrice}`}</span>
                  </div>
                </Link>
              </li>
            </React.Fragment>
          )
        )}
        <li className={`${styles.viewAll} swiper-slide`}>查看全部</li>
      </ol>
    </div>
  );
};
export default productList;



