import React from "react";
import { Carousel, WingBlank } from "antd-mobile";
import "antd-mobile/lib/carousel/style/index.css";
import "./carouselRegister.module.css";

class CarouselPhoto extends React.Component {
  state = { focusList: this.props.focusList };
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite={true}
          swipeSpeed={4}
          autoplayInterval={5000}
          dots={true}
          doStyle={{
            width: ".53333rem",
            height: ".05333rem",
            display: "inline-block",
            background: "#ccc",
            opacity: ".4"
          }}
          dotActiveStyle={{
            opacity: 1,
            backgroundColor: "#fff"
          }}
        >
          {this.state.focusList.map(({ picUrl }) => (
            <a
              key={picUrl}
              style={{
                display: "inline-block",
                width: "100%",
                height: this.state.imgHeight
              }}
            >
              <img
                alt={""}
                src={picUrl}
                style={{ verticalAlign: "top", width: "100%" }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default CarouselPhoto;



