import React from "react";
import styles from "./productMake.module.css";
import Icon from "icon/createIcon";

const product = ({ overallStyle, header, iconName, productList }) => {
  return (
    <div className={overallStyle}>
      <div className={styles.wrapperStyle}>
        <h3 className={styles.header}>
          {header}
          <Icon name={iconName} />
        </h3>
        <div className={styles.list}>
          {productList.map(item => (
            <React.Fragment key={item.id}>
              <div className={styles.item}>
                <div className={styles.itemTitle}>
                  <div className={styles.label}>
                    <div className={styles.itemName}>{item.name}</div>
                    <span>{`${item.floorPrice}元起`}</span>
                    {item.newOnShelf ? (
                      <span className={styles.newIcon}>上新</span>
                    ) : (
                      ""
                    )}
                  </div>
                  <img alt={""} src={item.picUrl} className={styles.picUrl} />
                </div>
              </div>
            </React.Fragment>
          ))}
        </div>
      </div>
    </div>
  );
};

export default product;



