import { interval, merge, Subject, of } from "rxjs";
import { sample, switchMap, filter, map ,skipUntil} from "rxjs/operators";

export default () => {
  const reducerObservable$ = new Subject();
  const timeHistory$ = new Subject();
  const firstTime = reducerObservable$.pipe(
    filter(({ type }) => type === "presentTime"),
    map(({ timeList }) => timeHistory$.next(timeList))
  );

  /*
  * @repetitionTimes 开始计时时间，等待时间记录的开始，如果开始就每秒发出值
  * @timeJudge 时间计算
  * @numberJudge 前缀添加 如果小于10就加上一个0前缀
  * */
  const repetitionTimes = interval(1000).pipe(
      skipUntil(timeHistory$)
  );
  const timeJudge = ({ Second, Minute, Hour }) => {
    if (Second === 0) {
      return Minute === 0
        ? { Hour: Hour - 1, Minute: 59, Second: 59 }
        : { Hour: Hour, Minute: Minute - 1, Second: 59 };
    } else {
      return { Hour: Hour, Minute: Minute, Second: Second - 1 };
    }
  };
  const numberJudge = timeList => {
    const timeListCopy = Object.assign({}, timeList);
    for (const index in timeListCopy) {
      timeListCopy[index] < 10
        ? (timeListCopy[index] = "0" + timeListCopy[index])
        : timeListCopy[index];
    }
    return timeListCopy;
  };
  const nextTimes = timeHistory$.pipe(
    sample(repetitionTimes),
    switchMap(timeList => {
      timeHistory$.next(timeJudge(timeList));
      reducerObservable$.next(numberJudge(timeJudge(timeList)));
      return of(timeJudge(timeList));
    })
  );
  const ObservableTurnover = merge(nextTimes, firstTime);
  return { timeHistory$, reducerObservable$, ObservableTurnover };
};



