import React from "react";
import styles from "./phototWrapper.module.css";

const photoWrapper = ({ picUrl, originalPrice, discountsPrice }) => (
  <div className={styles.wrapper}>
    <img src={picUrl} alt="" className={styles.photo} />
    <div className={styles.priceTag}>
      <span>{`¥${discountsPrice}`}</span>
      <span className={styles.originalPrice}>{`¥${originalPrice}`}</span>
    </div>
  </div>
);

export default photoWrapper;



