import React from "react";
import styles from "./header.module.css";
import Icon from "icon/createIcon";

const header = ({ title, headerBackground, headerColor, allBackground }) => {
  return (
    <div
      className={styles.header}
      style={{
        backgroundColor: headerBackground,
        color: headerColor
      }}
    >
      <span>{title}</span>
      <div className={styles.all} style={{ backgroundColor: allBackground }}>
        查看全部
        <Icon name="return" />
      </div>
    </div>
  );
};

export default header;



