import React from "react";
import styles from "./item.module.css";
import { Link } from "@reach/router";

export default ({
  listPicUrl,
  simpleDesc,
  name,
  couponPrice,
  colorNum,
  wait,
  id
}) => {
  return (
    <li className={styles.overallWrapper}>
     <Link to={`./detail/item/${id}`}>
        <div className={styles.photoWrapper}>
          {wait ? "" : <img src={listPicUrl} className={styles.photo} alt="" />}
          <div className={styles.advantage}>{simpleDesc}</div>
          {colorNum ? (
            <div className={styles.colorNum}>
              <div>{colorNum}</div>
              <div>色</div>
              <div>可</div>
              <div>选</div>
            </div>
          ) : (
            ""
          )}
        </div>
        <div className={styles.name}>{name}</div>
        <div className={styles.money}>¥{couponPrice}</div>
      </Link>
    </li>
  );
};



